import that from '../../main'
import { valiateFun } from '@/network/networks'
import { myDate} from '@/assets/js/common'
export var dataRules = (rule,value,callback) =>{
        if(value == ""){
            return callback();
        }

        var userRes = window.sessionStorage.getItem("userRes");
        userRes= JSON.parse(userRes);
        if(userRes){
            if(rule.field == "contact" && userRes.contact == value){
                return callback();
            }else if(rule.field == "qq" && userRes.qq == value){
                return callback();
            }else if(rule.field == "weixin" && userRes.weixin == value){
                return callback();
            }
        }
        console.log(userRes)
    
        var fieldTxt = rule.fieldTxt;
    
        if(rule.field == "contact"){
            if(!(/^[1][3,4,5,7,8,9][0-9]{9}$/.test(value))){
                return callback(new Error(""))
            }
        }else if(rule.field == "qq"){
            if(!(/^[1-9][0-9]{4,14}$/.test(value))){
                return callback(new Error("QQ"))
            }
        }else if(rule.field == "weixin"){
            if(!(/^[a-zA-Z][a-zA-Z\d_-]{5,19}$/.test(value))){
                return callback(new Error(""))
            }
        }
    
        var myObj = {
            keyword:value,
            field:rule.field
        }
        //
        valiateFun(myObj).then(res => {
            console.log(res.data)
            if(res.data.code == "403"){
                var content = res.data.content
                var text=`
                    ${fieldTxt}<span style="color:red">${value}</span><br/>
                    <span style="color:red">${content.nickname}</span>,<br/>
                    <span style="color:red">${myDate(content.posttime, 1)}</span>,<br/>
                    
                `;
                that.$confirm(text, '', {
                    showCancelButton: false,
                    showConfirmButton: false,
                    dangerouslyUseHTMLString:true,
                    type: 'warning'
                })
                return callback(new Error(""+"fieldTxt"+""))
            }else{
                return callback()
            }
        })
    }
