<template>
    <div class="updataout">
        <el-dialog title="" width="60%" :visible = "showDialog" :close-on-click-modal="false" @close = "clickClose">
            <el-form :model="adduserForm" :rules="adduserRules" ref="adduserForm" label-width="100px" status-icon>
                <el-form-item label="" prop="nickname">
                    <el-input v-model.trim="adduserForm.nickname"></el-input>
                </el-form-item>
                <el-form-item label="" prop="contact">
                    <el-input v-model.trim="adduserForm.contact" ></el-input>
                </el-form-item>
                <el-form-item label="QQ" prop="qq">
                    <el-input v-model.trim="adduserForm.qq"></el-input>
                </el-form-item>
                <el-form-item label="" prop="weixin">
                    <el-input v-model.trim="adduserForm.weixin"></el-input>
                </el-form-item>
                <el-form-item label="" prop="zhuanye">
                    <el-input placeholder="" v-model="adduserForm.zhuanye" >
                        <el-select v-model="adduserForm.infoclass" slot="prepend" placeholder="" class="mySelect">
                            <el-option v-for="item in infoClassList" :key="item.id" :value="item.classname" :label="item.classname"></el-option>
                        </el-select>
                    </el-input>
                </el-form-item>

                <el-form-item label="" prop="laiyuan">
                    <el-input v-model="adduserForm.laiyuan"></el-input>
                </el-form-item>
                <el-form-item label="" prop="dizhi">
                    <el-input v-model="adduserForm.dizhi"></el-input>
                </el-form-item>
                <el-form-item label="" prop="kefu">
                    <el-select v-model="adduserForm.kefu" placeholder="" multiple clearable>
                        <el-option v-for="item in kefuList" :key="item.id" :value="item.classname" :label="item.classname"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-row :gutter="20">
                        <el-col :span="6">
                            <el-button type="primary" style="width:100%" @click.stop="onUpdata"></el-button>
                        </el-col>
                        <el-col :span="2">
                            <el-button style="width:100%" @click.stop="onReset"></el-button>
                        </el-col>
                    </el-row>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
import Vue from 'vue'
import { Row, Col, Form, FormItem, Input,Select,Option, Button, Message, MessageBox, Dialog} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
import { dataRules } from '@/assets/js/validate'
import { getKefuInfo, getRowUser, updataUser } from '@/network/networks'
Vue.prototype.$message = Message
Vue.prototype.$confirm = MessageBox.confirm;
export default {
    data(){
        return{
            showDialog:false,
            rowId:0,
            kefuList:[],
            infoClassList:[],
            adduserForm:{
                nickname:"",
                contact:'',
                qq:"",
                weixin:"",
                laiyuan:"",
                kefu:[],
                zhuanye:"",
                infoClass:""
            },
            adduserRules:{
                nickname:[
                    {required: true, message: '', trigger: 'blur'},
                    { min: 1, max: 6, message: ' 3  5 ', trigger: 'blur' }
                ],
                contact:[
                    { validator:dataRules, trigger:'blur'}
                ],
                qq:[
                    { validator:dataRules, trigger:'blur'}
                ],
                weixin:[
                    { validator:dataRules, trigger:'blur'}
                ],
                laiyuan:[
                    {required: true, message: '', trigger: 'blur'},
                ],
                zhuanye:[
                    {required: true, message: '', trigger: 'blur'},
                ],
                kefu:[
                    { required: true, message: '', trigger: 'change' }
                ]
            }
        }
    },
    watch:{
        rowId(val){
            this.getUserFun()
            // console.log(val)
        }
    },
    components:{
        'el-dialog':Dialog,
        'el-row':Row,
        'el-col':Col,
        'el-form':Form,
        'el-form-item':FormItem,
        'el-input':Input,
        'el-select':Select,
        'el-option':Option,
        'el-button':Button
    },
    mounted(){
        this.getKFInfo();
        var userRes = window.sessionStorage.getItem("userRes")
        if(userRes){
            window.sessionStorage.removeItem("userRes")
        }
        
    },
    methods:{
        //
        clickClose(){
            this.showDialog = false
            window.sessionStorage.removeItem("userRes")
        },
        //
        onUpdata(){
            this.$refs.adduserForm.validate(valid => {
                if(valid == true){
                    updataUser(this.adduserForm).then(res => {
                        this.$message.success("")
                        this.showDialog = false;
                        this.$parent.getAllData()
                        console.log(res)
                    })
                }
            })
            return;
            
        },
        //
        onReset(){
            this.getUserFun();
        },
        //
        getKFInfo(){
            getKefuInfo().then(res => {
                this.kefuList = res.data.kefu
                this.infoClassList = res.data.infoclass
            })
        },
        //
        getUserFun(){
            getRowUser(this.rowId).then(res => {
                this.$refs.adduserForm.resetFields()
                res.data.results.kefu = res.data.results.kefu.split("-")
                this.adduserForm = res.data.results
                var userRes = res.data.results
                userRes = JSON.stringify(userRes)
                window.sessionStorage.setItem("userRes",userRes)
            })
        }
    }
}
</script>
<style lang="less" scoped>
.updataout{

}
.mySelect{
    width: 120px;
}
</style>