
<template>
   <div class="alluser">
        <el-card shadow="never" class="homeTop">
            <el-row>
                <el-col :span="11">
                    <el-input v-model="myParams.keyword" placeholder="" @keyup.enter.native ="clickSearch"></el-input>
                </el-col>
                <el-col :span="1">
                    <el-button type="primary" plain icon="el-icon-search" @click="clickSearch"></el-button>
                </el-col>
            </el-row>
        </el-card>
        <div class="userallmain">
            <el-table height="500" style="width: 100%" :data="userdata" stripe border>
                <el-table-column
                    type="index"
                    label=""
                    width="50"
                    fixed
                    >
                </el-table-column>
                <el-table-column
                    prop="nickname"
                    label="">
                </el-table-column>
                <el-table-column
                    label="">
                    <template slot-scope="scope">
                        <i class="el-icon-time" style="font-size:12px"></i>
                        <span style="margin-left: 5px; font-size:12px; color:#666;">{{ myDate(scope.row.posttime) }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="contact"
                    label=""
                    width="100"
                    >
                </el-table-column>
                <el-table-column
                    prop="weixin"
                    label="weixin">
                </el-table-column>
                <el-table-column
                    prop="qq"
                    label="QQ">
                </el-table-column>
                <el-table-column
                    label="">
                    <template slot-scope="scope">
                        <span class="classname">{{scope.row.infoclass}}</span>
                        {{ scope.row.zhuanye }}
                    </template>
                </el-table-column>
                <el-table-column
                    prop="laiyuan"
                    label="">
                </el-table-column>
                <el-table-column
                    prop="dizhi"
                    label="">
                </el-table-column>
                <el-table-column
                    prop="kefu"
                    label="">
                </el-table-column>
                <el-table-column
                    label="">
                    <template slot-scope="scope">
                        <el-tooltip effect="dark" content="" placement="top">
                            <el-button type="primary" icon="el-icon-edit" size = "mini" @click="clickUpdata(scope.row.id)"></el-button>
                        </el-tooltip>
                        <el-tooltip effect="dark" content="" placement="top">
                            <el-button type="danger" icon="el-icon-delete" size = "mini" @click="clickDel(scope.row.id)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-card shadow="never" class="pages">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :page-sizes="[20, 50, 100]"
                :page-size="20"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>

        <Updata ref="updataDialog"></Updata>
    </div>
</template>

<script>
import { Card, Row, Col, Input, Button, Table, TableColumn, Tooltip,Pagination, MessageBox } from 'element-ui'
import { userAllFun, delUserRow } from '@/network/networks'
import { myDate } from '@/assets/js/common'
import Updata from './children/Updata'
import Vue from 'vue'
Vue.prototype.$confirm = MessageBox.confirm;
export default {
    data(){
        return{
            keywords:"",
            userdata:[],
            total:0,
            myParams:{
                num:20,
                page:1,
                keyword:""
            }
        }
    },
    mounted(){
        this.getAllData();
    },
    methods:{
        //
        clickUpdata(val){
            this.$refs.updataDialog._data.showDialog = true
            this.$refs.updataDialog.rowId = val
            // this.$refs.updataDialog.getUserFun()
        },
        //
        clickDel(val){
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'error'
            }).then(res => {
                delUserRow(val).then(res => {
                    this.getAllData();
                })
            })
            
        },
        //
        clickSearch(){
            this.getAllData();
        },
        //
        handleSizeChange(e){
            this.myParams.num = e
            this.getAllData();
            console.log(e)
        },
        //
        handleCurrentChange(e){
            this.myParams.page = e
            this.getAllData();
            console.log(e)
        },
        //
        myDate(time){
            return myDate(time,0)
        },
        //
        getAllData(){
            userAllFun(this.myParams).then(res => {
                this.userdata = res.data.results
                this.total = res.data.num
            })
        }
    },
    components:{
        'el-card':Card,
        'el-row':Row,
        'el-col':Col,
        'el-input':Input,
        'el-button':Button,
        'el-table':Table,
        'el-table-column':TableColumn,
        'el-tooltip':Tooltip,
        'el-pagination':Pagination,
        Updata
    }
}
</script>

<style lang="less" scoped>
.userallmain{
    margin-top:20px
}
.classname{
    position: absolute;
    top:5px;
    right:10px;
    font-size: 12px;
    transform: scale(0.8);
    color: #409eff;
}
.pages{
    margin:20px 0 50px;
    text-align: center;
}
</style>
